class WeightForm extends React.Component{
  constructor(props){
    super(props);
    this.state = {value: '', weights: [], valid: false};
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.greedyOutput = this.greedyOutput.bind(this);
  }

  handleChange(event){
    this.setState({value: event.target.value.replace(/\D/,'')})
    this.setState({weights: []});
  }

  handleSubmit(event){
    event.preventDefault();
    this.greedyWeight(this.state.value); 
  }

  greedyWeight(val){
    if (this.state.weights.length == 0){
      const intVal = parseInt(val);
      if (intVal % 5 == 0 && intVal >= 45 && intVal <= 1000000){
        var sum = 45;
        const weights = [45, 35, 25, 10, 5, 2.5];
        this.setState({valid: true});
        while (sum != intVal){
          for (let i=0; i< weights.length; i ++){
            if ((sum + (weights[i] * 2)) <= intVal){
              sum += weights[i] * 2;
              this.state.weights.push(weights[i]);
              this.setState({weights:this.state.weights});
              break;
            }
          }
        }
      } else{
          this.state.weights.push(<li className= "output-list" key={0}> Not a valid weight! </li>);
          this.state.weights.push(<sub className= "output-list" key={1} style={{position:'relative', left: '-45%'}}> Must be divisible by 5, and the base bar weight is 45lbs. </sub>);
          this.setState({weights:this.state.weights});
          this.setState({valid: false});

      }
    }
  }

  greedyOutput(){
    if (this.state.valid){
      var counts = {};
      this.state.weights.forEach(function (x) { counts[x] = (counts[x] || 0) + 1; });
      var ret = []
      for (var key in counts){
        var plural;
        if (counts.hasOwnProperty(key)){
          if(counts[key] > 1){
            plural = 's';
          } else{
            plural ='';
          }
          ret.push(<li className= "output-list"> 
          <span style={{color: 'yellow'}}> {counts[key]} </span>
          set{plural} of 
          <span style={{color: 'yellow'}}> {key}lbs </span> 
          </li>)
        }
      }
      return (
        ret.map((weight) => weight));
    } else{
      return (
        this.state.weights.map((weight) => weight));
    }
  }

  render(){
    return (
      <form className = "weight-form" onSubmit={this.handleSubmit}>
        <h2 className="label-wrapper">
          <label>
            Target Weight
          </label>
        </h2>
        <input
          value ={this.state.value}
          onChange={this.handleChange}
          className="input"
        />
        <button className="button" type="submit">
          Calculate
        </button>
        <h2 className="label-output">
          <this.greedyOutput />
        </h2>
      </form>    
    );
  }
}
  
